import "./user.css";

export const createUser = () => {
  // Define the modules and their respective permissions
  const modules = [
    {
      name: "User Management",
      permissions: ["Read", "Update", "Create", "Delete"],
    },
    { name: "Reports", permissions: ["Read", "Create", "Export"] },
    { name: "Settings", permissions: ["Read", "Update"] },
  ];

  const users = ["Mouy", "Tey", "Houy", "Noynoy"];

  // Create the main container for the user table
  const userPermission = document.createElement("div");
  userPermission.classList.add("user-table");

  // Create the table element
  const table = document.createElement("table");

  // Create the table header
  const thead = document.createElement("thead");
  const headerRow = document.createElement("tr");

  // First column for "Name"
  const thName = document.createElement("th");
  thName.textContent = "Name";
  headerRow.appendChild(thName);

  // Create header columns for each module and its permissions
  modules.forEach((module) => {
    module.permissions.forEach((permission) => {
      const th = document.createElement("th");
      th.textContent = `${module.name} - ${permission}`;
      headerRow.appendChild(th);
    });
  });

  thead.appendChild(headerRow);

  // Create the table body
  const tbody = document.createElement("tbody");

  // Add rows for each user
  users.forEach((user) => {
    const row = document.createElement("tr");

    // Create and append the name cell
    const nameCell = document.createElement("td");
    nameCell.textContent = user;
    row.appendChild(nameCell);

    // Create and append a checkbox for each permission under each module
    modules.forEach((module) => {
      module.permissions.forEach((permission) => {
        const checkboxCell = document.createElement("td");
        const checkbox = document.createElement("input");
        checkbox.type = "checkbox";
        checkbox.name = `${module.name}-${permission}-${user}`;
        checkbox.value = `${permission}`;

        // Event listener to change background color when checkbox is checked
        checkbox.addEventListener("change", (event) => {
          if (event.target.checked) {
            row.style.backgroundColor = "lightgreen";
          } else {
            row.style.backgroundColor = "";
          }
        });

        checkboxCell.appendChild(checkbox);
        row.appendChild(checkboxCell);
      });
    });

    tbody.appendChild(row);
  });

  // Append the table elements to the userPermission div
  table.appendChild(thead);
  table.appendChild(tbody);
  userPermission.appendChild(table);

  // Append the userPermission div to the document body or a specific element
  document.body.appendChild(userPermission);

  return userPermission;
};
